/**
* CSS file to hold styles that apply across the application
*/

@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap");

html,
body {
  font-family: "Montserrat", sans-serif;
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
}

div#main {
  display: flex;
  flex-direction: column;
  width: 100vw;
}

section {
  position: relative;
  width: 100vw;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  left: 50%;
  object-fit: cover;
  pointer-events: none;
  position: absolute;
  top: 0;
  transform: translate(-50%, 0);
  width: 100vmax;
  z-index: -1;
}

h1 {
  font-family: "Cormorant Garamond", serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: 0.8;
  margin: 0;
}

h2 {
  font-size: 2rem;
  font-weight: 400;
  margin: 0;
  padding-top: 1rem;
}

h3 {
  font-weight: 400;
  font-size: 1.25rem;
  margin: 0;
}

p {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 0;
  font-weight: 300;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 330px 300px;
  margin: 20px auto;
}

.container .box {
  border-radius: 10px;
  border: 1px solid rgb(204, 204, 204);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  text-align: center;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

/*fully responsive*/

/* General styles */
.min-height {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1; /* Ensures the background stays behind content */
}

.content-wrapper {
  position: absolute;
  top: 5%;
  left: 5%;
  width: 90%;
  max-width: 700px; /* Limits width for larger screens */
  text-align: left;
}

.responsive-heading {
  top: auto;
  font-size: clamp(2rem, 5vw, 4rem); /* Adjusts font size dynamically */
  margin: 0;
}

.responsive-subheading {
  font-size: clamp(1.5rem, 3vw, 2.5rem);
  margin: 0.5rem 0 0;
}

.arrow-wrapper {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
}

.arrow-icon {
  height: 3rem;
  width: 3rem;
  animation: bounce 2s infinite; /* Optional bounce animation */
}

/* Optional bounce animation */
@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .content-wrapper {
    top: 3%;
    left: 3%;
    width: 94%;
    text-align: center; /* Center-align content for smaller screens */
  }

  .arrow-icon {
    height: 2rem;
    width: 2rem;
  }
}

/* General styles */
.portfolio-section {
  padding: 2rem 1rem;
  background-color: #f9f9f9;
}

.portfolio-heading {
  text-align: center;
  font-size: clamp(2rem, 4vw, 3rem);
  margin-bottom: 2rem;
}

.portfolio-content {
  display: flex;
  flex-wrap: wrap; /* Allows items to wrap for smaller screens */
  justify-content: center;
  gap: 2rem;
}

.portfolio-image-wrapper {
  flex: 1 1 40%; /* Image takes up 40% of the row */
  max-width: 400px; /* Limit the maximum width */
}

.portfolio-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 10px;
}

.portfolio-projects {
  flex: 1 1 50%; /* Projects take up 50% of the row */
  max-width: 600px;
}

.portfolio-box {
  margin-bottom: 1.5rem;
  padding: 1rem;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.portfolio-box:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.portfolio-title {
  font-size: 1.5rem;
  color: #333;
}

.portfolio-description {
  font-size: 1rem;
  color: #666;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .portfolio-content {
    flex-direction: column; /* Stack items vertically */
    align-items: center;
  }

  .portfolio-image-wrapper {
    flex: 1 1 100%; /* Image takes full width */
  }

  .portfolio-projects {
    flex: 1 1 100%; /* Projects take full width */
    text-align: center; /* Center-align text for smaller screens */
  }

  .portfolio-box {
    text-align: left;
  }
}

@media (max-width: 480px) {
  .portfolio-title {
    font-size: 1.25rem;
  }

  .portfolio-description {
    font-size: 0.9rem;
  }
}

/* General styles */
.about-section {
  position: relative;
  padding: 2rem 1rem;
  background-color: #f9f9f9;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1; /* Keeps background behind content */
}

.about-content {
  background-color: gray;
  width: 90%;
  max-width: 800px;
  padding: 2rem;
  margin: 2rem auto;
  border-radius: 10px;
  text-align: center;
  color: #fff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.about-heading {
  font-size: clamp(2rem, 4vw, 3rem); /* Dynamic font size */
  margin-bottom: 1.5rem;
}

.about-description {
  font-size: 1.25rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.about-skills-list {
  text-align: left;
  columns: 2; /* Create columns for wider screens */
  column-gap: 2rem;
  font-size: 1.25rem;
  margin: 2rem 0;
  padding: 0;
}

.about-skill-item {
  margin-bottom: 0.5rem;
}

.about-quote {
  padding: 1rem 2rem 0;
  font-size: 1.2rem;
  line-height: 1.5;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .about-content {
    width: 95%; /* Reduce width for tablets */
    padding: 1.5rem;
  }

  .about-skills-list {
    columns: 1; /* Stack skills vertically on smaller screens */
  }

  .about-quote {
    padding: 1rem 1rem 0; /* Adjust padding for smaller screens */
  }
}

@media (max-width: 480px) {
  .about-heading {
    font-size: 1.75rem;
  }

  .about-description {
    font-size: 1rem;
    display: flex;
    flex-wrap: wrap; /* Allows items to wrap for smaller screens */
    justify-content: center;
  }

  .about-skills-list {
    font-size: 1rem;
  }

  .about-quote {
    font-size: 1rem;
  }
}

/* General styles */
.home-section {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}

.home-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1; /* Keep the background behind other content */
}

.home-content {
  position: absolute;
  top: 20%; /* Adjust for better alignment on different screens */
  left: 5%;
  width: 90%;
  max-width: 600px; /* Limit width for large screens */
  text-align: left;
}

.home-heading {
  font-size: clamp(2.5rem, 5vw, 4rem); /* Responsive font size */
  color: #535151;
  margin: 0;
}

.home-subheading {
  font-size: clamp(1.5rem, 3vw, 2.5rem);
  color: #5a4f4f;
  margin: 1rem 0 0;
}

.home-arrow {
  position: absolute;
  bottom: 3rem;
  left: 50%;
  transform: translateX(-50%);
}

.arrow-icon {
  height: 3rem;
  width: 3rem;
  animation: bounce 2s infinite; /* Optional bounce animation */
}

/* Bounce animation */
@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .home-content {
    top: 15%;
    left: 5%;
    text-align: center; /* Center-align for smaller screens */
  }

  .home-heading {
    font-size: 2rem;
  }

  .home-subheading {
    font-size: 1.5rem;
  }

  .arrow-icon {
    height: 2.5rem;
    width: 2.5rem;
  }
}

@media (max-width: 480px) {
  .home-content {
    top: 10%;
    left: 5%;
  }

  .home-heading {
    font-size: 1.75rem;
  }

  .home-subheading {
    font-size: 1.25rem;
  }

  .arrow-icon {
    height: 2rem;
    width: 2rem;
  }
}
